import { AnnotationTool } from '@cornerstonejs/tools';
import { servicesManager } from '../../../../platform/app/src/App';
import { StackViewport } from '@cornerstonejs/core';
import { getEnabledElement } from '@cornerstonejs/core';
import type { Types } from '@cornerstonejs/core';

import * as csTools from '@cornerstonejs/tools';

const DECAY_SUBTYPE = '1';
const PERIRADICULAR_LESION_SUBTYPE = '10';
const PERI_IMPLANTITIS_SUBTYPE = '16';
const CALCULUS_SUBTYPE = '20';
const FURCATION_DEFECT_SUBTYPE = '25';
const BROKEN_INSTRUMENT_SUBTYPE = '30';
const IMPACTED_TOOTH_SUBTYPE = '45';
const RESIDUAL_ROOT_SUBTYPE = '55';
const ARTICULAR_ANOMALY_SUBTYPE = '80';
const CYST_SUBTYPE = '89';
const ENDODONTIC_ROOT_CANAL_SUBTYPE = '110';
const CAROTID_CALCIFICATION_SUBTYPE = '200';

const colorsSquare: { [subtype: string]: string; others: string } = {
  [DECAY_SUBTYPE]: 'hsl(51, 100%, 50%)',
  [PERIRADICULAR_LESION_SUBTYPE]: 'hsl(4, 81%, 75%)',
  [PERI_IMPLANTITIS_SUBTYPE]: 'hsl(311, 100%, 26%)',
  [CALCULUS_SUBTYPE]: 'hsl(180, 100%, 20%)',
  [FURCATION_DEFECT_SUBTYPE]: 'hsl(293, 94%, 58%)',
  [BROKEN_INSTRUMENT_SUBTYPE]: 'hsl(173, 68%, 78%)',
  [IMPACTED_TOOTH_SUBTYPE]: 'hsl(112, 94%, 58%)',
  [RESIDUAL_ROOT_SUBTYPE]: 'hsl(175, 94%, 58%)',
  [ARTICULAR_ANOMALY_SUBTYPE]: 'hsl(259, 94%, 58%)',
  [CYST_SUBTYPE]: 'hsl(330, 100%, 84%)',
  [ENDODONTIC_ROOT_CANAL_SUBTYPE]: 'hsl(261, 78%, 34%)',
  [CAROTID_CALCIFICATION_SUBTYPE]: 'hsl(230, 100%, 61%)',
  others: 'hsl(180, 72%, 38%)',
};

interface DiseaseAnnotation extends csTools.Types.Annotation {
  data: {
    handles: {
      points: Types.Point3[];
      activeHandleIndex: number | null;
      textBox: {
        hasMoved: boolean;
        worldPosition: Types.Point3;
        worldBoundingBox: {
          topLeft: Types.Point3;
          topRight: Types.Point3;
          bottomLeft: Types.Point3;
          bottomRight: Types.Point3;
        };
      };
    };
    label: string;
    subtype: string;
  };
}

class DiseaseTool extends AnnotationTool {
  static toolName = 'Disease';
  _selectedAnnotationUID: string | null = null; // Tracks the annotation clicked by the user

  _renderingViewport: any;
  editData: {
    annotation: any;
    viewportIdsToRender: string[];
    handleIndex?: number;
    movingTextBox?: boolean;
    hasMoved?: boolean;
  } | null;
  isDrawing: boolean;
  isHandleOutsideImage: boolean;

  constructor(
    toolProps: csTools.Types.PublicToolProps = {},
    defaultToolProps: csTools.Types.ToolProps = {
      supportedInteractionTypes: ['Mouse', 'Touch'],
      configuration: {
        shadow: true,
        preventHandleOutsideImage: false,
        statsCalculator: csTools.utilities.math.BasicStatsCalculator,
      },
    }
  ) {
    super(toolProps, defaultToolProps);
  }

  /**
   * it is used to draw the disease annotation in each
   * request animation frame. It calculates the updated cached statistics if
   * data is invalidated and cache it.
   *
   * @param enabledElement - The Cornerstone's enabledElement.
   * @param svgDrawingHelper - The svgDrawingHelper providing the context for drawing.
   */
  renderAnnotation = (
    enabledElement,
    svgDrawingHelper: csTools.Types.SVGDrawingHelper
  ): boolean => {
    let renderStatus = false;
    const { viewport } = enabledElement;
    this._renderingViewport = viewport;
    const { element } = viewport;

    let annotations = csTools.annotation.state.getAnnotations(this.getToolName(), element);

    if (!annotations?.length) {
      return renderStatus;
    }

    annotations = this.filterInteractableAnnotationsForElement(element, annotations);

    if (!annotations?.length) {
      return renderStatus;
    }

    for (let i = 0; i < annotations.length; i++) {
      const annotation = annotations[i] as DiseaseAnnotation;
      const { annotationUID, data } = annotation;
      const { points, activeHandleIndex } = data.handles;
      const canvasCoordinates = points.map(p => viewport.worldToCanvas(p));

      const isSelectedForRendering =
        annotation.highlighted || this._selectedAnnotationUID === annotationUID;

      const color =
        this._selectedAnnotationUID === annotationUID
          ? 'red'
          : colorsSquare[data.subtype] || colorsSquare.others;
      const lineWidth = 2;
      const lineDash = '0';

      // If rendering engine has been destroyed while rendering
      if (!viewport.getRenderingEngine()) {
        console.warn('Rendering Engine has been destroyed');
        return renderStatus;
      }

      let activeHandleCanvasCoords;

      if (!csTools.annotation.visibility.isAnnotationVisible(annotationUID)) {
        continue;
      }

      if (
        !csTools.annotation.locking.isAnnotationLocked(annotation) &&
        !this.editData &&
        activeHandleIndex !== null
      ) {
        // Not locked or creating and hovering over handle, so render handle.
        activeHandleCanvasCoords = [canvasCoordinates[activeHandleIndex]];
      }

      if (activeHandleCanvasCoords) {
        const handleGroupUID = '0';

        csTools.drawing.drawHandles(
          svgDrawingHelper,
          annotationUID,
          handleGroupUID,
          activeHandleCanvasCoords,
          {
            color: 'transparent',
          }
        );
      }

      const dataId = `${annotationUID}-disease`;
      const diseaseUID = '0';
      drawDiseaseByCoordinates(
        svgDrawingHelper,
        annotationUID,
        diseaseUID,
        canvasCoordinates,
        {
          color,
          lineDash,
          lineWidth,
          cornerRadius: 10,
        },
        dataId
      );

      renderStatus = true;

      const options = {
        background: '',
        color: color,
        fontFamily:
          '-apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif',
        fontSize: '15px',
        lineDash: 0,
        lineWidth: 0,
        shadow: true,
        visibility: isSelectedForRendering,
        padding: 10,
      };

      if (!options.visibility) {
        data.handles.textBox = {
          hasMoved: false,
          worldPosition: <Types.Point3>[0, 0, 0],
          worldBoundingBox: {
            topLeft: <Types.Point3>[0, 0, 0],
            topRight: <Types.Point3>[0, 0, 0],
            bottomLeft: <Types.Point3>[0, 0, 0],
            bottomRight: <Types.Point3>[0, 0, 0],
          },
        };
        continue;
      }

      const textLines = [`${data.label}`];

      if (!data.handles.textBox.hasMoved) {
        const canvas = viewport.canvas;
        const context = canvas.getContext('2d');
        context.font = `${options.fontSize} ${options.fontFamily}`;
        const textWidth = context.measureText(textLines[0]).width;

        // Estimate textbox height to vertically center it against the annotation.
        const approxCharHeight = parseInt(options.fontSize, 10);
        const textBoxHeight = approxCharHeight + options.padding * 3;
        const topMostY = Math.min(...canvasCoordinates.map(p => p[1]));
        const bottomMostY = Math.max(...canvasCoordinates.map(p => p[1]));
        const annotationHeight = bottomMostY - topMostY;
        const newY = topMostY + annotationHeight / 2 - textBoxHeight / 2; // - options.padding;

        // Default position is to the right of the annotation
        let canvasTextBoxCoords =
          csTools.utilities.drawing.getTextBoxCoordsCanvas(canvasCoordinates);

        // Measure the text width to accurately position the text box.
        const textBoxWidth = textWidth + options.padding * 2;
        const viewportWidth = viewport.element.clientWidth;

        // Check if the textbox would go out of the viewport on the right
        if (canvasTextBoxCoords[0] + textBoxWidth > viewportWidth) {
          // It overflows. Position it to the left of the annotation.
          const leftMostX = Math.min(...canvasCoordinates.map(p => p[0]));

          const newX = leftMostX - textBoxWidth;

          canvasTextBoxCoords = [newX, newY];
        } else {
          canvasTextBoxCoords = [canvasTextBoxCoords[0], newY];
        }

        data.handles.textBox.worldPosition = viewport.canvasToWorld(canvasTextBoxCoords);
      }

      const textBoxPosition = viewport.worldToCanvas(data.handles.textBox.worldPosition);

      const textBoxUID = '1';
      const boundingBox = drawLinkedTextBox(
        svgDrawingHelper,
        annotationUID,
        textBoxUID,
        textLines,
        textBoxPosition,
        canvasCoordinates,
        {},
        options
      );

      const { x: left, y: top, width, height } = boundingBox;

      data.handles.textBox.worldBoundingBox = {
        topLeft: viewport.canvasToWorld([left, top]),
        topRight: viewport.canvasToWorld([left + width, top]),
        bottomLeft: viewport.canvasToWorld([left, top + height]),
        bottomRight: viewport.canvasToWorld([left + width, top + height]),
      };
    }

    return renderStatus;
  };

  moveDentascanIndex = (evt, annotation) => {
    const {
      hangingProtocolService,
      cornerstoneViewportService,
      panoramicService,
      viewportGridService,
    } = servicesManager.services;

    const eventDetail = evt.detail;
    const { element } = eventDetail;
    // Cursor was moved with toolSelectedCallback
    csTools.cursors.elementCursor.resetElementCursor(element);

    if (hangingProtocolService.getActiveProtocol().protocol?.id === 'panoramicViewport') {
      const { points } = annotation.data.handles;
      const panoramicVpId = cornerstoneViewportService.getViewportIds()[1];
      const panoramicViewport = cornerstoneViewportService.getCornerstoneViewport(
        panoramicVpId
      ) as StackViewport;
      const { imageData } = panoramicViewport.getImageData();
      const center = (points[0][0] + points[3][0]) / 2;
      const x = Math.round(imageData.worldToIndex([center, 0, 0])[0]);

      const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
      panoramicService.setDentascanIndex(displaySetUID, x);
    }
  };

  // Override mouseClickCallback from BaseTool to handle selection/deselection
  mouseClickCallback = (evt: csTools.Types.EventTypes.InteractionEventType): void => {
    const eventDetail = evt.detail;
    const { element, currentPoints } = eventDetail;
    const canvasCoords = currentPoints.canvas;

    const annotations = csTools.annotation.state.getAnnotations(this.getToolName(), element);

    let clickedAnnotation: DiseaseAnnotation | undefined;

    if (annotations?.length) {
      for (let i = 0; i < annotations.length; i++) {
        const annotation = annotations[i] as DiseaseAnnotation;
        // Use the tool's proximity configuration to check if the click is near an annotation
        if (this.isPointNearTool(element, annotation, canvasCoords, this.configuration.proximity)) {
          clickedAnnotation = annotation;
          break;
        }
      }
    }

    if (clickedAnnotation) {
      // An annotation was clicked, so select it
      this._selectedAnnotationUID = clickedAnnotation.annotationUID;
      // Call the original toolSelectedCallback logic (which will set annotation.highlighted = true)
      this.toolSelectedCallback(evt, clickedAnnotation);
    } else {
      // No annotation was clicked, deselect any currently selected one
      if (this._selectedAnnotationUID !== null) {
        this._selectedAnnotationUID = null;
        // Trigger a re-render to update the visual state of the deselected annotation
        const enabledElement = getEnabledElement(element);
        const { renderingEngine } = enabledElement;
        const viewportIdsToRender =
          csTools.utilities.viewportFilters.getViewportIdsWithToolToRender(
            element,
            this.getToolName()
          );
        csTools.utilities.triggerAnnotationRenderForViewportIds(
          renderingEngine,
          viewportIdsToRender
        );
      }
    }
  };

  toolSelectedCallback = (
    evt: csTools.Types.EventTypes.InteractionEventType,
    annotation: DiseaseAnnotation
  ): void => {
    const eventDetail = evt.detail;
    const { element } = eventDetail;

    annotation.highlighted = true;
    this._selectedAnnotationUID = annotation.annotationUID; // Set selected UID here too

    const viewportIdsToRender = csTools.utilities.viewportFilters.getViewportIdsWithToolToRender(
      element,
      this.getToolName()
    );

    this.editData = {
      annotation,
      viewportIdsToRender,
      movingTextBox: false,
    };

    this._activateModify(element);

    csTools.cursors.elementCursor.hideElementCursor(element);

    const enabledElement = getEnabledElement(element);
    const { renderingEngine } = enabledElement;

    csTools.utilities.triggerAnnotationRenderForViewportIds(renderingEngine, viewportIdsToRender);

    evt.preventDefault();

    this.moveDentascanIndex(evt, annotation);
  };

  handleSelectedCallback = (
    evt: csTools.Types.EventTypes.InteractionEventType,
    annotation: DiseaseAnnotation,
    handle: csTools.Types.ToolHandle
  ): void => {
    const eventDetail = evt.detail;
    const { element } = eventDetail;
    const { data } = annotation;

    this._selectedAnnotationUID = annotation.annotationUID; // Set selected UID here too
    annotation.highlighted = true;

    let movingTextBox = false;
    let handleIndex;

    if ((handle as csTools.Types.TextBoxHandle).worldPosition) {
      movingTextBox = true;
    } else {
      handleIndex = data.handles.points.findIndex(p => p === handle);
    }

    // Find viewports to render on drag.
    const viewportIdsToRender = csTools.utilities.viewportFilters.getViewportIdsWithToolToRender(
      element,
      this.getToolName()
    );

    this.editData = {
      annotation,
      viewportIdsToRender,
      handleIndex,
      movingTextBox,
    };
    this._activateModify(element);

    csTools.cursors.elementCursor.hideElementCursor(element);

    const enabledElement = getEnabledElement(element);
    const { renderingEngine } = enabledElement;

    csTools.utilities.triggerAnnotationRenderForViewportIds(renderingEngine, viewportIdsToRender);

    evt.preventDefault();

    this.moveDentascanIndex(evt, annotation);
  };

  addNewAnnotation = () => null;
  cancel = () => null;

  /**
   * It returns if the canvas point is near the provided annotation in the provided
   * element or not. A proximity is passed to the function to determine the
   * proximity of the point to the annotation in number of pixels.
   *
   * @param element - HTML Element
   * @param annotation - Annotation
   * @param canvasCoords - Canvas coordinates
   * @param proximity - Proximity to tool to consider
   * @returns Boolean, whether the canvas point is near tool
   */
  isPointNearTool = (
    element: HTMLDivElement,
    annotation: DiseaseAnnotation,
    canvasCoords: Types.Point2,
    proximity: number
  ): boolean => {
    const enabledElement = getEnabledElement(element);
    const { viewport } = enabledElement;

    const { data } = annotation;
    const { points } = data.handles;

    const canvasPoint1 = viewport.worldToCanvas(points[0]);
    const canvasPoint2 = viewport.worldToCanvas(points[3]);

    const rect = this._getRectangleImageCoordinates([canvasPoint1, canvasPoint2]);

    const point = [canvasCoords[0], canvasCoords[1]];
    const { left, top, width, height } = rect;

    const distanceToPoint = csTools.utilities.math.rectangle.distanceToPoint(
      [left, top, width, height],
      point as Types.Point2
    );

    if (distanceToPoint <= proximity) {
      return true;
    }

    return false;
  };

  _endCallback = (evt: csTools.Types.EventTypes.InteractionEventType): void => {
    const eventDetail = evt.detail;
    const { element } = eventDetail;

    const { annotation, viewportIdsToRender, hasMoved } = this.editData;
    const { data } = annotation;

    if (!hasMoved) {
      return;
    }

    data.handles.activeHandleIndex = null;

    this._deactivateModify(element);

    csTools.cursors.elementCursor.resetElementCursor(element);

    const { renderingEngine } = getEnabledElement(element);

    this.editData = null;
    this.isDrawing = false;

    if (this.isHandleOutsideImage && this.configuration.preventHandleOutsideImage) {
      csTools.annotation.state.removeAnnotation(annotation.annotationUID);
    }

    csTools.utilities.triggerAnnotationRenderForViewportIds(renderingEngine, viewportIdsToRender);
  };

  /**
   * Add event handlers for the modify event loop, and prevent default event prapogation.
   */
  _activateModify = element => {
    csTools.state.isInteractingWithTool = true;

    element.addEventListener(csTools.Enums.Events.MOUSE_UP, this._endCallback);
    element.addEventListener(csTools.Enums.Events.MOUSE_CLICK, this._endCallback);

    element.addEventListener(csTools.Enums.Events.TOUCH_END, this._endCallback);
    element.addEventListener(csTools.Enums.Events.TOUCH_TAP, this._endCallback);
  };

  /**
   * Remove event handlers for the modify event loop, and enable default event propagation.
   */
  _deactivateModify = element => {
    csTools.state.isInteractingWithTool = false;

    element.removeEventListener(csTools.Enums.Events.MOUSE_UP, this._endCallback);
    element.removeEventListener(csTools.Enums.Events.MOUSE_CLICK, this._endCallback);

    element.removeEventListener(csTools.Enums.Events.TOUCH_END, this._endCallback);
    element.removeEventListener(csTools.Enums.Events.TOUCH_TAP, this._endCallback);
  };

  _getRectangleImageCoordinates = (
    points: Array<Types.Point2>
  ): {
    left: number;
    top: number;
    width: number;
    height: number;
  } => {
    const [point0, point1] = points;

    return {
      left: Math.min(point0[0], point1[0]),
      top: Math.min(point0[1], point1[1]),
      width: Math.abs(point0[0] - point1[0]),
      height: Math.abs(point0[1] - point1[1]),
    };
  };

  removeDiseaseAnnotation = (uid) => {
    csTools.annotation.state.removeAnnotation(uid);
  }

}

function _getHash(annotationUID: string, drawingElementType: string, nodeUID: string): string {
  return `${annotationUID}::${drawingElementType}::${nodeUID}`;
}

function drawDiseaseByCoordinates(
  svgDrawingHelper: csTools.Types.SVGDrawingHelper,
  annotationUID: string,
  rectangleUID: string,
  canvasCoordinates: Types.Point2[],
  options = {},
  dataId = ''
): void {
  const {
    color,
    width: _width,
    lineWidth,
    lineDash,
    cornerRadius,
  } = Object.assign(
    {
      color: 'rgb(0, 255, 0)',
      width: '2',
      lineWidth: undefined,
      lineDash: undefined,
      cornerRadius: 0,
    },
    options
  );

  // for supporting both lineWidth and width options

  const strokeWidth = lineWidth || _width;

  const svgns = 'http://www.w3.org/2000/svg';
  const svgNodeHash = _getHash(annotationUID, 'rect', rectangleUID);
  const existingRect = svgDrawingHelper.getSvgNode(svgNodeHash);

  const [topLeft, topRight, bottomLeft, bottomRight] = canvasCoordinates;

  const width = Math.hypot(topLeft[0] - topRight[0], topLeft[1] - topRight[1]);
  const height = Math.hypot(topLeft[0] - bottomLeft[0], topLeft[1] - bottomLeft[1]);

  const center = [(bottomRight[0] + topLeft[0]) / 2, (bottomRight[1] + topLeft[1]) / 2];
  const leftEdgeCenter = [(bottomLeft[0] + topLeft[0]) / 2, (bottomLeft[1] + topLeft[1]) / 2];
  const angle =
    (Math.atan2(center[1] - leftEdgeCenter[1], center[0] - leftEdgeCenter[0]) * 180) / Math.PI;

  const attributes = {
    x: `${center[0] - width / 2}`,
    y: `${center[1] - height / 2}`,
    width: `${width}`,
    height: `${height}`,
    stroke: color,
    fill: 'transparent',
    transform: `rotate(${angle} ${center[0]} ${center[1]})`,
    'stroke-width': strokeWidth,
    'stroke-dasharray': lineDash,
    rx: `${cornerRadius}`,
    ry: `${cornerRadius}`,
  };

  if (existingRect) {
    csTools.drawing.setAttributesIfNecessary(attributes, existingRect);

    svgDrawingHelper.setNodeTouched(svgNodeHash);
  } else {
    const svgRectElement = document.createElementNS(svgns, 'rect');

    if (dataId !== '') {
      svgRectElement.setAttribute('data-id', dataId);
    }

    csTools.drawing.setNewAttributesIfValid(attributes, svgRectElement);

    svgDrawingHelper.appendNode(svgRectElement, svgNodeHash);
  }
}

function drawLinkedTextBox(
  svgDrawingHelper: csTools.Types.SVGDrawingHelper,
  annotationUID: string,
  textBoxUID: string,
  //
  textLines: Array<string>,
  textBoxPosition: Types.Point2,
  annotationAnchorPoints: Array<Types.Point2>,
  textBox: unknown,
  options = {}
  // TODO: yCenter as an option
): SVGRect {
  const mergedOptions = Object.assign(
    {
      handleRadius: '6',
      centering: {
        x: false,
        y: true, // yCenter,
      },
    },
    options
  );

  // Draw the text box
  const canvasBoundingBox = csTools.drawing.drawTextBox(
    svgDrawingHelper,
    annotationUID,
    textBoxUID,
    textLines,
    textBoxPosition,
    mergedOptions
  );

  return canvasBoundingBox;
}

export default DiseaseTool;
